@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

/* ------ Add your CSS below this line (or @import your other CSS files). ------- */
.main-background {
	background-image: conic-gradient(from 152deg at 50% 50%, #0057E1 0%, #F9C5B4 68.44503837719299%, #0057E1 100%);
}

.main-masked-image {
	mask-image: url("/images/mask.svg");
	mask-position: 0% 0%;
	mask-size: 100% 100%;
}

.effect--tilted-image {
	transform:
		perspective(500px) rotateY(-20deg);
	box-shadow:
		rgba(0, 0, 0, 0.024) 0px 0px 0px 1px,
		rgba(0, 0, 0, 0.05) 0px 1px 0px 0px,
		rgba(0, 0, 0, 0.03) 0px 0px 8px 0px,
		rgba(0, 0, 0, 0.1) 0px 20px 30px 0px;
	animation: slidein 2s;
}

@keyframes slidein {
	from {
		transform: perspective(500px) translateX(300px) rotateY(0deg);
	}

	to {
		transform: perspective(500px) translateX(0px) rotateY(-20deg);
	}
}

@media (max-width: 600px) {
	.effect--tilted-image {
		transform:
			rotateY(0deg);
		box-shadow:
			rgba(0, 0, 0, 0.024) 0px 0px 0px 1px,
			rgba(0, 0, 0, 0.05) 0px 1px 0px 0px,
			rgba(0, 0, 0, 0.03) 0px 0px 8px 0px,
			rgba(0, 0, 0, 0.1) 0px 20px 30px 0px;
	}
}

.effect--pulsing-heart {
	animation: pulsingheart 1.5s infinite;
}

@keyframes pulsingheart {
	from {
		transform: scale(1) rotate(12deg);
	}

	40% {
		transform: scale(1.25) rotate(18deg);
	}

	to {
		transform: scale(1) rotate(12deg);
	}
}